import React, { useState, useEffect } from "react";
import ReactPaginate from "react-paginate";
import './Pagination.css'; // CSS dosyasn ayr bir dosyaya tayabilirsiniz

const Pagination = ({ pageNumber, info, updatePageNumber }) => {
  const [width, setWidth] = useState(window.innerWidth);

  const updateDimensions = () => {
    setWidth(window.innerWidth);
  };

  useEffect(() => {
    window.addEventListener("resize", updateDimensions);
    return () => window.removeEventListener("resize", updateDimensions);
  }, []);

  const pageChange = (data) => {
    updatePageNumber(data.selected + 1);
  };

  return (
    <>
      <ReactPaginate
        forcePage={pageNumber > 0 ? pageNumber - 1 : 0} // forcePage sfrdan kk olamaz
        marginPagesDisplayed={1}  // Balang ve son sayfalar her zaman grnsn
        pageRangeDisplayed={2}  // Maksimum 6 buton gster
        pageCount={info?.pages > 0 ? info.pages : 1}  // pageCount sfrdan kk olamaz
        onPageChange={pageChange}
        className="pagination justify-content-center my-4 gap-4"
        nextLabel="Next"
        previousLabel="Prev"
        previousClassName="fs-5 prev"
        nextClassName="fs-5 next"
        activeClassName="active"
        pageClassName="page-item"
        pageLinkClassName="page-link"
      />
    </>
  );
};

export default Pagination;
