const FilterButton = ({ input, task, updatePageNumber, index, name }) => {
  return (
    <div>
      <style>
        {`
          .x:checked + label {
            background-color: #0b5ed7;
            color: white;
          }

          input[type="radio"] {
            display: none;
          }
        `}
      </style>
      <div className="form-check">
      <input
  className="form-check-input x"
  type="radio"
  name={name}  // Grup ad
  id={`${name}-${index}`}  // Benzersiz ID
  onChange={() => {
    console.log(`Selected ${name}:`, input);  // Konsolda seilen deeri kontrol et
    task(input);  // Seilen deeri st seviyeye bildir
    updatePageNumber(1);  // Sayfay sfrla
  }}
/>
        <label className="btn btn-outline-primary" htmlFor={`${name}-${index}`}>
          {input}
        </label>
      </div>
    </div>
  );
};

export default FilterButton;